@import './../../common/styles/mixins';
@import './../../common/styles/variables';

.packs {
  display: flex;
  justify-content: center;
  align-items: flex-start;

  .wrap {
    margin-top: 2rem;
    max-width: 200vh;
    min-height: 80vh;
    display: grid;
    grid-template-columns: 25% 75%;
    grid-template-rows: 1fr;

    .sidebar {
      background-color: #ECECF9;

      .btnTitle {
        margin-top: 2rem;
        font-weight: 600;
        font-size: 1rem;
        color: black;
      }

      .btnBox {
        max-width: 12.25rem;
        margin: 0 auto;
        margin-top: 1.2rem;
        display: flex;
        justify-content: center;
        align-items: center;

        .btn {
          height: 2.3rem;
          margin: 0;
          padding: 0;
          border: 0;
          border-radius: 0;
          width: 100%;

          &:first-child {
            background-color: white;
            color: #000;
          }

          &:last-child {
            background-color: #9A91C8;
            color: white;
          }
        }
      }

      .inputTitle {
        height: 2.3rem;
        margin-top: 2.8rem;
        font-weight: 600;
        font-size: 1rem;
      }
    }


    .listBlock {
      padding: 1.5rem 3rem;
      background-color: white;
      overflow: scroll;

      .listTitle {
        font-weight: 600;
        font-size: 1.4rem;
      }

      .addPack {
        width: 100%;
        margin-top: .9rem;
        display: flex;
        align-items: center;
        position: relative;

        .inputSearchWrap {
          width: 100%;
          display: flex;
          justify-content: space-between;
          position: relative;

          .inputSearch {
            width: 100%;
            height: 36px;
            padding-left: 2.5rem;
            border: 1px solid darkgrey;
            background-color: #ECECF9;
          }

          &::before {
            content: "";
            width: 1rem;
            height: 1rem;
            background-image: url("./../../assets/images/icons/search-icon.png");
            background-repeat: no-repeat;
            position: absolute;
            top: 50%;
            left: 12px;
            transform: translate(0, -50%);
          }
        }

        .addBtn {
          margin-left: 1.5rem;
          width: 120px;
          height: 100%;
          font-weight: 500;
          font-size: 1rem;
        }
      }

      .table {
        //height: 670px;
        margin: 1.5rem 0;
        filter: drop-shadow(0px 4px 14px rgba(45, 46, 70, 0.15));

        .tableHeader {
          height: 3rem;
          display: grid;
          grid-template-columns: 15% 10% 25% 25% 25%;
          //grid-template-columns: repeat(5, 1fr);
          grid-template-rows: 1fr;
          column-gap: 10px;
          background-color: lightblue;
          text-align: center;

          .tableItem {
            align-self: center;
            font-weight: 600;
            font-size: 1.1rem;
          }
        }

        .packRow {
          height: 48px;
          padding: 0 10px;
          display: grid;
          grid-template-columns: 15% 10% 25% 25% 25%;
          grid-template-rows: 1fr;
          column-gap: 10px;

          &:nth-child(2n+1) {
            background-color: #F8F7FD;
          }

          .packRowItem {
            align-self: center;
            margin: 0 5px;
            font-weight: 500;
            font-size: 0.8rem;
            color: black;

            &:last-child {
              padding-right: 20px;
            }
          }

          .packRowBtn {
            width: 48px;
            height: 24px;
            margin-right: 10px;
            font-weight: 500;
            font-size: 0.8rem;
            line-height: 24px;
            background-color: red;
            color: white;
            border: 0;
          }

          .packRowLink {
            width: 43px;
            height: 24px;
            //padding: 3px;
            margin-right: 5px;
            font-weight: 500;
            font-size: 0.8rem;
            line-height: 24px;
            display: inline-block;
            background-color: #B0C4DE;
          }
        }
      }
    }
  }
}